body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 60px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-header {
  background-color: #222;
  padding: 0 20px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.App-title {
  font-size: 1.5em;
  margin-right: 40px;
}

.header-btn {
  margin: 10px;
  border: none;
  background: white;
  font-size: 0.75em;
  padding: 0.5em 1.5em;
}

.lists {
  display: -ms-flexbox;
  display: flex;
  overflow: scroll;
}

.list {
  -ms-flex-preferred-size: 300px;
      flex-basis: 300px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  overflow: scroll;
}

.list-heading {
  text-align: center;
}

.list-ul {
  padding: 0;
  margin: 0.5em;
}

.list-li {
  margin: 1em 0;
  padding: 0.5em;
  border: 1px solid #777;
  list-style: none;
  border-radius: 4px;
}


